* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Lato", sans-serif;
}

img {
  max-width: 100%;
  height: auto;
}

.header {
  padding: 15px 0;
}

.navigation {
  list-style: none;
  padding: 0;
  margin: 0;
}

.navigation-link {
  text-decoration: none;
  text-transform: uppercase;
  color: #939699;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.5px;
}

.navigation-link:hover {
  color: #66696c;
}

button {
  cursor: pointer;
  background-color: transparent;
  background-repeat: no-repeat;
  border: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  overflow: inherit;
}

.button {
  padding: 10px 20px;
  background: #4592ff;
  border-radius: 20px;
  border: none;
  color: #fff;
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
  display: inline-block;
  align-items: center;
  text-decoration: none;
}

.button-icon,
.button-price {
  margin-right: 10px;
}

.button:hover {
  background-color: #2981ff;
}

#button-addon2 {
  border-radius: 0 20px 20px 0;
  padding: 10px 15px;
}

#button-addon2:hover {
  background-color: #1c69d7;
}

.button-icon {
  margin-left: 10px;
}

.button-icon:last-child {
  margin-right: 0;
}

.cart-count {
  margin-left: 10px;
}

.show-goods .slider,
.show-goods .special-offers {
  display: none;
}

.long-goods {
  display: none;
  min-height: 100vh;
  padding-bottom: 50px;
}

.show-goods .long-goods {
  display: block;
}

.slide {
  padding-top: 90px;
  padding-bottom: 110px;
  min-height: 568px;
}

.slide-1 {
  background: #b4d1e0 url("../img/slide-image-1.png") no-repeat top right 30%;
}

.slide-2 {
  background: #b9e0b4 url("../img/slide-image-2.png") no-repeat top right 30%;
  background-size: contain;
}

.slide-3 {
  background: #f0a0a0 url("../img/slide-image-3.png") no-repeat top right 30%;
}

.label {
  background-color: #3dc47e;
  border-radius: 3px;
  padding: 5px 12px;
  color: #fff;
  font-size: 12px;
  line-height: 15px;
}

.slide-title {
  color: #262d33;
  font-size: 55px;
  line-height: 70px;
  font-family: "Roboto Slab", serif;
  margin-top: 20px;
  margin-bottom: 15px;
}

.slide-description {
  font-size: 18px;
  line-height: 30px;
  color: #4b5157;
  max-width: 335px;
  margin-bottom: 40px;
}

.slider {
  position: relative;
}

.slider-nav {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
  z-index: 9;
}

.slider-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.card {
  min-height: 370px;
  border-radius: 10px;
  padding-top: 60px;
  padding-left: 46px;
  padding-right: 46px;
  padding-bottom: 80px;
}

.card-1 {
  background: #e3e2e3 url("../img/card-image-1.jpg") no-repeat top right;
  background-size: cover;
}

.card-2 {
  background: #000 url("../img/card-image-2.jpg") no-repeat top right;
  background-size: cover;
}

.card-3 {
  padding-top: 35px;
  background: #ccd5e6 url("../img/card-image-3.png") no-repeat top right 10%;
  background-size: contain;
}

.card-4 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 35px;
  padding-left: 25px;
  background: #ff4f52 url("../img/card-image-4.png") no-repeat top right;
  background-size: contain;
}

.goods-card-btn {
  display: block;
  width: 100px;
  margin: auto auto 0;
}

.goods-card-btn .button-price {
  margin: 0;
}

.cart-icon {
  display: none;
  padding-left: 21px;
}

.visible-icon {
  display: inherit;
}

.d-none {
  display: none;
}

.card-title {
  color: #262d33;
  font-size: 25px;
  line-height: 30px;
  font-family: "Roboto Slab", serif;
  max-width: 222px;
  margin: 0;
}

.card-title.large {
  max-width: 420px;
  font-size: 40px;
  line-height: 50px;
  margin-top: 30px;
  margin-bottom: 20px;
}

.card-text {
  max-width: 176px;
  color: #4b5157;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 25px;
}

.card-text.large {
  max-width: 320px;
}

.card-price {
  color: #ffffff;
  font-size: 14px;
  line-height: 20px;
  font-weight: 900;
}

.text-light {
  color: #fff;
}

.section-title {
  font-family: "Roboto Slab", serif;
  font-size: 25px;
  line-height: 30px;
  color: #262d33;
}

.center {
  text-align: center;
}

.more {
  text-decoration: none;
  color: #262d33;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
  padding: 10px;
}

.goods-card {
  display: flex;
  flex-direction: column;
  position: relative;
  text-align: center;
  padding: 30px 35px 35px;
  min-height: 440px;
}

.goods-card:hover {
  cursor: pointer;
}

.goods-card:hover .goods-title,
.goods-card:hover .goods-description,
.more:hover {
  color: #7a55e7;
  color: #7a55e7;
}

.goods-card:hover .label {
  background: #2cce7a;
}

.goods-card:hover .goods-image {
  -webkit-transform: scale(0.8); /* Safari and Chrome */
  -moz-transform: scale(0.8); /* Firefox */
  -ms-transform: scale(0.8); /* IE 9 */
  -o-transform: scale(0.8); /* Opera */
  transform: scale(0.8);
}

.goods-card .label {
  position: absolute;
  left: 18px;
  top: 18px;
  z-index: 1;
}

.goods-link {
  text-decoration: none;
}

.goods-title {
  margin-top: 15px;
  margin-bottom: 5px;
  color: #262d33;
  font-size: 16px;
  line-height: 25px;
  font-family: "Roboto Slab", serif;
}

.goods-image {
  object-fit: contain;
  height: 190px;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.goods-description {
  color: #4b5157;
  font-size: 14px;
  line-height: 20px;
}

.goods-price {
  color: #262d33;
  margin-top: 30px;
  font-size: 18px;
  line-height: 30px;
  font-weight: 900;
}

.button-four {
  margin-top: auto;
}

.footer-menu {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.footer {
  background: #8f79d4;
  padding-top: 45px;
  padding-bottom: 40px;
  color: #fff;
}

.footer-menu-item:not(:last-child) {
  margin-right: 40px;
}

.footer-menu-link {
  color: #fff;
  text-decoration: none;
  font-weight: 900;
  font-size: 14px;
  line-height: 20px;
}

.footer-menu-link:hover {
  color: #d5d5d5;
}

.social-link {
  text-decoration: none;
  margin-left: 15px;
}

.footer-text {
  color: #e7d9ff;
  font-size: 14px;
  line-height: 20px;
}

.footer-line {
  opacity: 0.5;
}

.top-link {
  text-decoration: none;
  color: #fff;
}

.top-link-text {
  margin-right: 15px;
}

.scroll-link:hover {
  cursor: pointer;
}

.payment-logo {
  vertical-align: middle;
  margin-right: 25px;
}

.empty-goods-wrapper {
  display: flex;
  justify-content: center;
  margin: 25px 0;
  border: 1px solid #4592ff;
  border-radius: 50px;
}

.goods-empty {
  font-size: 24px;
  color: #4592ff;
  padding: 300px;
}

.goods-empty.small {
  padding: 100px;
  color: #3dc47e;
}

.empty-goods-wrapper.green {
  border: 1px solid #3dc47e !important;
}
