.body {
  background: #b9a4ff;
  font-family: sans-serif;
  font-size: 10px;
}

.div {
  padding: 150px;
}

.form {
  background: #fff;
  padding: 4em 4em 2em;
  max-width: 400px;
  margin: auto;
  -webkit-box-shadow: 0 0 1em #222;
  box-shadow: 0 0 1em #222;
  border-radius: 2px;
}

.form h2 {
  margin: 0 0 30px 0;
  padding: 10px;
  text-align: center;
  font-size: 30px;
  color: #939699;
  border-bottom: solid 1px #e5e5e5;
}

.form p {
  margin: 0 0 3em 0;
  position: relative;
}

.form input {
  display: block;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  width: 100%;
  outline: none;
  margin: 0;
}

.form input[type="text"],
.form input[type="password"] {
  background: #fff;
  border: 1px solid #dbdbdb;
  font-size: 1.6em;
  padding: 0.8em 0.5em;
  border-radius: 2px;
}

.form input[type="text"]:focus,
.form input[type="password"]:focus {
  background: #fff;
}

.form span {
  display: block;
  background: #f9a5a5;
  padding: 2px 5px;
  color: #666;
}

.form input[type="submit"] {
  background: rgba(148, 186, 101, 0.7);
  border-radius: 2px;
  border: none;
  color: #fff;
  cursor: pointer;
  display: block;
  font-size: 2em;
  line-height: 1.6em;
  margin: 2em 0 0;
  outline: none;
  padding: 0.8em 0;
  text-shadow: 0 1px #68b25b;
}

.form input[type="submit"]:hover {
  background: #b9a4ff;
  text-shadow: 0 1px 3px #b9a4ff;
}

.form label {
  position: absolute;
  left: 8px;
  top: 12px;
  color: #999;
  font-size: 16px;
  display: inline-block;
  padding: 4px 10px;
  font-weight: 400;
  background-color: rgba(255, 255, 255, 0);
}

.form label.floatLabel {
  top: -11px;
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 14px;
}

.text {
  font-size: 15px;
  text-decoration: none;
  color: #49484a;
  text-align: center;
}

.navlink,
navlink:visited {
  text-decoration: none;
  color: #4592ff;
}

.navlink:hover {
  color: #a186f8;
}

